@use 'partials' as *;
*, *:before, *:after{
    box-sizing: border-box;
}

html{
    font-size: 10px;
}
body{
    display: grid;
    place-items: center;
    min-height: 100vh;
    font-size: 1.3rem;
    margin: 0;
    font-family: 'Barlow Semi Condensed', sans-serif;
}

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
}
.container{
    display: flex; 
    justify-content: space-between;
    width: 75%;
    .left{
        display: flex;
        flex-flow: column;
        justify-content: space-between;
       width:74.333%;
    //    margin-right: 4rem;
        .top{
            display: flex;
            flex-flow: row;
            justify-content: space-between;
            width: 100%;
            // background-color: purple;
            color: $white;
            // padding-top: 2rem;
            // margin-bottom: 3rem;
            height: 48%;

            .top-left{
                display: flex;
                flex-flow: column;
                justify-content: space-between;
                flex-basis: 66.666%;
                background: url(images/bg-pattern-quotation.svg) no-repeat;
                background-position: 80% 0;
                background-size: 28%;
                background-color: $violet;
                border-radius: 1.6rem;
                padding: 5rem;
                // margin-right: 4rem;
                 
                .h2{
                    color: $color2;
                }
                .headings{
                    display: flex;
                    align-items: center;
                    // margin-bottom: 1rem;
                   color: $color1;
                   
                   h4{
                       font-weight: 600;
                   }
                    p{
                        font-weight: 500;
                        opacity: 50%;
                        color: $color2;
                    }
                  
                    .image{
                         margin-right: 1.6rem;
                        img{
                        
                            border-radius: 50%;
                        }
                    }
                
                }

                .text-bottom{
                    // margin: 2rem 0;
                    color: $color2;
                    opacity: 70%;
                    line-height: 1.5;
                    font-weight: 500;
                }
            }
            .top-right{
                display: flex;
                flex-flow: column;
                justify-content: space-between;
                flex-basis: 30.666%;
                background: $grayish;
                padding: 5rem;
                border-radius: 1.6rem;

                .h2{
                    color: $color2;
                }

                .headings{
                    display: flex;
                    align-items: center;
                    // margin-bottom: 1rem;
                    color: $color1;
                   
                    h4{
                        font-weight: 600;
                    }
                     p{
                         font-weight: 500;
                         opacity: 50%;
                         color: $color2;
                     }

                    .image{
                            
                        margin-right: 1.6rem;
                        img{
                            border-radius: 50%;
                        }
                    }
                }

                .text-bottom{
                    margin: 1.6rem 0;
                    color: $color2;
                    opacity: 70%;
                    line-height: 1.5;
                    font-weight: 500;
                }
            }


        }


        .bottom{
            display: flex;
            flex-flow: row;
            justify-content: space-between;
            width: 100%;
            height: 48%;
            // background-color: purple;
            // padding-top: 2rem;

            .top-left{
                flex-basis: 30.666%;
                 background-color: $white;
                border-radius: 1.6rem;
                padding: 5rem;
                box-shadow: .5rem .5rem 1rem .7rem hsla(210, 46%, 95%, .5);
                // margin-right: 4rem;

                .headings{
                    display: flex;
                    align-items: center;
                    margin-bottom: 1rem;
                    color: $grayish;

                    h2{
                        color: $color2;
                    }
                   
                    h4{
                        font-weight: 600;
                    }
                     p{
                         font-weight: 500;
                         opacity: 50%;
                         color: $grayish;
                     }
                  
                    .image{
                         margin-right: 1.6rem;
                        img{
                        
                            border-radius: 50%;
                        }
                    }
                
                }

                .text-bottom{
                    margin: 2rem 0;
                    color: $grayish;
                    opacity: 70%;
                    line-height: 1.5;
                    font-weight: 500;
                }
            }
            .top-right{
                flex-basis: 66.666%;
                background: $blackish;
                color: $white;
                padding: 5rem;
                border-radius: 1.6rem;

                .headings{
                    display: flex;
                    align-items: center;
                    margin-bottom: 1rem;
                    color: $color1;
                   
                    h4{
                        font-weight: 600;
                    }
                     p{
                         font-weight: 500;
                         opacity: 50%;
                         color: $color2;
                     }


                    .image{
                            
                        margin-right: 1.6rem;
                        img{
                            border-radius: 50%;
                        }
                    }
                }

                .text-bottom{
                    margin: 1.6rem 0;
                    color: $color2;
                    opacity: 70%;
                    line-height: 1.5;
                    font-weight: 500;
                }
            }


        }

    }
    

    .right{
        width: 23.666%;
        background: $white;
        padding: 5rem;
        border-radius: 1.6rem;
        box-shadow: .1rem .5rem 1rem .7rem hsla(210, 46%, 95%, .5);
        
        .headings{
            display: flex;
            align-items: center;
            margin-bottom: 1rem;
            color: $color2;

            .text{
                h4{
                    color: $grayish;
                    font-weight: 600;
                }
                p{
                    color: $grayish;
                    opacity: 50%;
                    font-weight: 500;
                }
            }
            

            .image{
                    
                margin-right: 1.6rem;
                img{
                    border-radius: 50%;
                }
            }
        }
        
        
        .text-bottom{
            margin: 1.6rem 0;
            color: $grayish;
            line-height: 1.5;
            opacity: 70%;
            font-weight: 500;
        }
    }
}


.m1{width: 8.333%}
.m2{width: 16.666%}
.m3{width: 24.999%}
.m4{width: 33.333%}
.m5{width: 41.666%}
.m6{width: 49.999%}
.m7{width: 58.333%}
.m8{width: 66.666%}
.m9{width: 74.999%}
.m10{width: 83.333%}



@media screen and (min-width: 1267px) and (max-width: 1540px){
    
    .m11{width: 91.666%}
}
 
@media screen and (min-width: 1170px) and (max-width: 1267px){

    .m12{width: 99.999%}
}


@media screen and (min-width: 520px) and (max-width: 1170px) {
    .container{
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        

        .left{
            width: 70%;
            margin-top: 4rem;
            
        }
        .left .top, .left .bottom{
            width: 100%;
            flex-direction: column;
            justify-content: space-between;


            .top-right, .top-left, .bottom-left, .bottom-right{
                  width: 100%;
                  margin-bottom: 4rem;
            }
        }

        .right{
            width: 70%;
            margin-bottom: 4rem;
        }
    }
}
@media screen and (max-width: 520px) {
 
    .container{
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        
        

        .left{
            width: 100%;
            margin-top: 4rem;
            
        }
        .left .top, .left .bottom{
            width: 100%;
            flex-direction: column;
            justify-content: space-between;


            .top-right, .top-left, .bottom-left, .bottom-right{
                  width: 100%;
                  margin-bottom: 4rem;
            }
        }

        .right{
            width: 100%;
            margin-bottom: 4rem;
        }
    }
}